<template>
  <div class="panel">
    <CerrarSesion />
    <section class="text-center space-y-10 space-x-5">
      <div class="text-black m-6">
        <h1 class="text-center text-blue-300 text-5xl font-semibold">
          Panel de Control
        </h1>
      </div>
    </section>
    <upload-photo />
  </div>
  <UpdateSlider />
</template>

<script>
import uploadPhoto from "@/components/uploadPhoto.vue";
import UpdateSlider from "@/components/UpdateSlider.vue";
import CerrarSesion from "@/components/CerrarSesion.vue";
import { db, auth } from "@/components/aq1sw2.js";
import Swal from "sweetalert2";

let coleccionData = [];
var coleccionRef = db.collection("app");

coleccionRef
  .get()
  .then((querySnapshop) => {
    querySnapshop.forEach((doc) => {
      return coleccionData.push(doc.data());
    });
  })
  .catch((error) => {});
export default {
  components: {
    uploadPhoto,
    UpdateSlider,
    CerrarSesion,
    Swal,
  },
  data() {
    return {
      formUser: {
        email: "",
        pass: "",
        // recordar: recordar,
      },
    };
  },
  created() {
    auth.onAuthStateChanged((user) => {
      if (user === null) {
        this.$router.push("/");
      } else {
        this.sesion = true;
        this.usuario = user.uid;
        this.formUser.email = user.email;
      }
    });
  }, //cerrar method
}; //cerrar  export default
</script>

