<template>
  <section class="text-center space-y-10 cards space-x-5">
    <div class="text-black grid grid-cols-1 md:grid-cols-3 gap-4 m-6">
      <figure class="bg-gray-100 rounded-xl p-8 shadow-xl">
        <img
          src="@/assets/logo-music.png"
          alt=""
          width="60"
          class="w-32 h-32 rounded-full mx-auto"
        />
        <h4 class="text-gray-500 text-center font-sans font-bold">ESTILOS</h4>

        <div class="pt-6 text-center space-y-1">
          <blockquote>
            <p class="text-lg font-semibold">
              Latina, clsica, chill out, disco, jazz, rock, electrnica,
              flamenca, regional, tnica, pop, actual, otros
            </p>
          </blockquote>
        </div>
      </figure>

      <figure class="bg-gray-100 rounded-xl p-8 shadow-xl">
        <img
          src="@/assets/logo-mic.png"
          alt=""
          width="60"
          class="w-32 h-32 rounded-full mx-auto"
        />
        <h4 class="text-gray-500 text-center font-sans font-bold">TIPO</h4>
        <div class="pt-6 text-center space-y-4">
          <blockquote>
            <p class="text-lg font-semibold">
              Solista, dueto, seccin, grabacin
            </p>
          </blockquote>
        </div>
      </figure>

      <figure class="bg-gray-100 rounded-xl p-8 shadow-xl">
        <img
          src="@/assets/preguntas.png"
          alt=""
          width="60"
          class="w-32 h-32 rounded-full mx-auto"
        />
        <h4 class="text-gray-500 text-center font-sans font-bold">Preguntas</h4>
        <div class="pt-6 text-center space-y-4">
          <blockquote>
            <p class="text-lg font-semibold"></p>
            <form
              class="grid grid-cols-2 md:grid-cols-1"
              @submit.prevent="enviar()"
            >
              <input
                type="text"
                class="my-1 mx-1 rounded-lg shadow-md"
                placeholder="Nombre y Apellido"
                v-model="msj.nombre"
              />
              <input
                type="email"
                class="my-1 mx-1 rounded-lg shadow-md"
                placeholder="Correo"
                v-model="msj.email"
              />
              <input
                type="text"
                class="my-1 mx-1 rounded-lg shadow-md"
                placeholder="Telefono"
                v-model="msj.telefono"
              />
              <input
                type="text"
                class="my-1 mx-1 rounded-lg shadow-md"
                placeholder="Comentario"
                v-model="msj.comentario"
              />
              <br />
              <button
                type="submit"
                class="
                  bg-gradient-to-r
                  hover:from-red-500
                  hover:to-red-500
                  bg-clip-text
                  text-transparent
                  from-red-400
                  to-purple-400
                  col-span-2
                  md:col-span-1
                "
              >
                Enviar pregunta
              </button>
            </form>
          </blockquote>
        </div>
      </figure>
    </div>
  </section>
</template>
<script>
import { db } from "./aq1sw2";
import Swal from "sweetalert2";
var moment = require("moment");
export default {
  name: "UpdateSlider",
  components: {
    db,
    Swal,
  },
  data() {
    return {
      msj: {
        nombre: "",
        email: "",
        telefonos: "",
        comentario: "",
        fecha: moment().format("LLLL"),
      },
      mensajes: [],
    };
  }, //Cierra Data
  mounted() {
    // this.enviar();
  },
  methods: {
    async enviar() {
      if (this.validator()) {
        db.collection("mensajes")
          .doc()
          .set({
            nombre: this.msj.nombre,
            email: this.msj.email,
            telefonos: this.msj.telefonos,
            comentario: this.msj.comentario,
            fecha: this.msj.fecha,
          })
          .then(() => {
            Swal.fire({
              title: "Enviado!",
              text: "Mensaje enviado con Exito",
              icon: "success",
              confirmButtonText: "Salir",
            });
            this.msj.nombre = "";
            this.msj.email = "";
            this.msj.telefonos = "";
            this.msj.comentario = "";
            this.msj.fecha = "";
          })
          .catch((error) => {
            Swal.fire({
              title: "Error!",
              text: error + " Ocurri un error",
              icon: "warning",
              confirmButtonText: "Salir",
            });
          });
      } else {
        Swal.fire({
          title: "Faltan!",
          text: "Tienes que rellenar algunos campos importantes ",
          icon: "warning",
          confirmButtonText: "Salir",
        });
      }
    },
    validator() {
      if (
        (this.msj.nombre.trim() == "" && this.msj.email.trim() == "") ||
        (this.msj.telefonos.trim() == "" && this.msj.comentario.trim() == "")
      ) {
        return false;
      } else {
        return true;
      }
    }, //fin ultimo metodo
  }, //fin method
}; // fin export defaul
</script>


<style>
.cards {
  background-color: #ffffff;
}
</style>