<template>
  <div
    class="
      min-h-screen
      flex
      items-center
      justify-center
      bg-gray-50
      py-12
      px-4
      sm:px-6
      lg:px-8
    "
  >
    <div class="max-w-md w-full space-y-8">
      <div>
        <img
          class="mx-auto h-12 w-auto"
          src="https://tailwindui.com/img/logos/workflow-mark-indigo-600.svg"
          alt="Workflow"
        />
        <h2 class="mt-6 text-center text-3xl font-extrabold text-gray-900">
          Backend
        </h2>
        <p class="mt-2 text-center text-sm text-gray-600">
          Desarrollado por Omar Suniaga
        </p>
      </div>
      <form
        class="mt-8 space-y-6"
        role="formUser"
        @submit.prevent="iniciarSesion()"
      >
        <div class="rounded-md shadow-sm -space-y-px">
          <div>
            <label for="email-address" class="sr-only">Email</label>
            <input
              v-model.trim="formUser.email"
              id="email"
              name="email"
              type="email"
              autocomplete="email"
              required=""
              class="
                appearance-none
                rounded-none
                relative
                block
                w-full
                px-3
                py-2
                border border-gray-300
                placeholder-gray-500
                text-gray-900
                rounded-t-md
                focus:outline-none
                focus:ring-indigo-500
                focus:border-indigo-500
                focus:z-10
                sm:text-sm
              "
              placeholder="Email address"
            />
          </div>
          <div>
            <label for="password" class="sr-only">Contrasea</label>
            <input
              v-model.trim="formUser.pass"
              id="password"
              name="password"
              type="password"
              autocomplete="current-password"
              required=""
              class="
                appearance-none
                rounded-none
                relative
                block
                w-full
                px-3
                py-2
                border border-gray-300
                placeholder-gray-500
                text-gray-900
                rounded-b-md
                focus:outline-none
                focus:ring-indigo-500
                focus:border-indigo-500
                focus:z-10
                sm:text-sm
              "
              placeholder="Password"
            />
          </div>
        </div>

        <div class="flex items-center justify-between">
          <div class="flex items-center">
            <input
              id="remember-me"
              name="remember-me"
              type="checkbox"
              v-model="formUser.recordar"
              class="
                h-4
                w-4
                text-indigo-600
                focus:ring-indigo-500
                border-gray-300
                rounded
              "
            />
            <label for="remember-me" class="ml-2 block text-sm text-gray-900">
              Recordar Cuenta
            </label>
          </div>

          <div class="text-sm">
            <a
              href="#"
              class="font-medium text-indigo-600 hover:text-indigo-500"
            >
              No funciona crear usuario
            </a>
          </div>
        </div>

        <div>
          <button
            type="submit"
            class="
              group
              relative
              w-full
              flex
              justify-center
              py-2
              px-4
              border border-transparent
              text-sm
              font-medium
              rounded-md
              text-white
              bg-indigo-600
              hover:bg-indigo-700
              focus:outline-none
              focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500
            "
          >
            <span class="absolute left-0 inset-y-0 flex items-center pl-3">
              <LockClosedIcon
                class="h-5 w-5 text-indigo-500 group-hover:text-indigo-400"
                aria-hidden="true"
              />
            </span>
            Entrar
          </button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import { LockClosedIcon } from "@heroicons/vue/solid";
import { db, auth } from "@/components/aq1sw2.js";
let coleccionData = [];
var coleccionRef = db.collection("app");
coleccionRef
  .get()
  .then((querySnapshop) => {
    querySnapshop.forEach((doc) => {
      return coleccionData.push(doc.data());
    });
  })
  .catch((error) => {});
export default {
  components: {
    LockClosedIcon,
  },
  data() {
    return {
      formUser: {
        username: "",
        email: "",
        pass: "",
        recordar: true,
      },
    };
  },
  mounted() {
    this.observador();
  },
  methods: {
    //permite el acceso directo al administrador una vez logeado
    observador() {
      auth.onAuthStateChanged((user) => {
        if (user) {
          this.username = user.uid;
          this.formUser.email = user.email;
          this.$router.push("/panel");
        }
      });
      // const user = auth.currentUser;
      // if (user) {
      //   console.log(user);
      // }
    },
    iniciarSesion() {
      let email = this.formUser.email;
      let password = this.formUser.pass;
      // this.formUser.recordar
      //   ? (dataLocal.recordar = true)
      //   : (dataLocal.recordar = false);
      auth
        .signInWithEmailAndPassword(email, password)
        .then((userCredential) => {
          // Signed in
          this.sesion = true;
          var user = userCredential.user;
          console.log("Ha iniciado sesion con el correo: ", user.email);
          this.$router.push("/panel");
        })
        .catch((error) => {
          this.sesion = false;
          console.error(error.message);
        });
    }, //cerrar ultimo metodos
  }, //cerrar method
}; //cerrar  export default
</script>
