<template>
  <div class="megusta mr-6">
    <div class="corazon">
      <button @click="verifyIP()">
        <svg width="75" height="75" viewBox="0 0 250.000000 250.000000">
          <g
            transform="translate(0.000000,250.000000) scale(0.100000,-0.100000)"
            :fill="[state ? '#fa0b0b' : '#EBEAEA']"
            stroke="none"
          >
            <path
              d="M755 2134 c-11 -2 -45 -9 -75 -15 -122 -25 -261 -121 -330 -229 -49
-76 -81 -173 -87 -265 -10 -149 54 -364 162 -542 176 -289 528 -608 778 -704
l57 -22 102 52 c156 78 276 168 438 331 286 285 425 548 437 825 5 100 3 117
-22 190 -32 97 -54 133 -120 204 -109 117 -235 173 -395 174 -78 0 -109 -5
-173 -27 -92 -31 -178 -86 -234 -150 l-40 -45 -49 50 c-67 69 -154 125 -228
148 -62 19 -185 33 -221 25z"
            />
          </g>
        </svg>
      </button>
    </div>
    <div v-if="count > 1" class="contador -mr-5">
      <span class="shadow-md">{{ count }}</span>
    </div>
  </div>
</template>

<script>
import { db } from "./aq1sw2";
const users = db.collection("likes");
export default {
  name: "MeGusta",
  data() {
    return {
      DDBB: [],
      IP: "",
      count: Number(),
      state: false,
      element: "",
    };
  },
  watch: {
    count: function (value) {
      // this.process();
    },
  },
  mounted() {
    this.process();
  },
  methods: {
    async process() {
      const DATA = await this.getData();
      this.IP = await this.getIP();
      this.state = await this.searchIP(this.IP);
      this.count = await this.getCountLike();
    },
    //Obtener DDBB
    async getData() {
      const data = await users.get();
      this.element = data.docs[data.docs.length];
      const result = data.docs.map((elem) => elem.data());
      this.DDBB.push(...result);
      return;
    },
    //Obtener el ultimo contador de megusta
    async getCountLike() {
      return this.DDBB.length;
    },
    //Obtener IP-Automatico
    async getIP() {
      const res = await fetch("https://api.ipify.org/?format=json");
      const data = await res.json();
      return data.ip;
    },
    //Buscar IP-Automatico registradas
    searchIP(IP) {
      return this.DDBB.map((e) => e.ip).includes(IP);
    },
    //verificar la IP al pulsar el boton "Me Gusta"
    async verifyIP() {
      if (this.state) {
        console.log("Gracias"); //corazon rojo
      } else {
        //falso?
        this.state = true; //corazon rojo
        this.count++; //
        const res = await fetch("https://api.ipify.org/?format=json"); //pido la ip de nuevo
        const data = await res.json(); //pido el json
        //Comprobando la existencia de la IP en la DDBB
        let result = this.DDBB.filter((e) => e.ip === data.ip); //pregunto si exite la ip
        result.length !== 0 ? null : this.like(data.ip); //si el resultado es distito a 0 vamos a otro metodo
      }
    },
    //registrar "me gusta"
    async like(IP) {
      try {
        //registramos el megusta
        await users
          .add({
            time: Date.now(),
            ip: IP,
            count: this.DDBB.length + 1,
          })
          .then((docRef) => {
            console.log("Me gusta ID: ", docRef.id); //mensaje en consola
          });
      } catch (error) {
        console.log(error);
      }
    },
  }, // Cierra Method
}; // Cierra export default
/****************LISTA DE TAREAS************************
 * Iniciando la pagina
 * obtener data => getDDBB()
 * obtener ultimo numero de contadores de megusta => getCountLike()
 * Si hace click en el corazon de megusta
 * Obtenemos ip automaticamente=> getIP()
 * Comparamos nuestra ip con las ip de data => searchIP()
 * Verificamos si la ip coincide con la DDBB para pintar Corazon automaticamente
 * Si la logitud de la busqueda es distita a 0 entonces mostramos corazon rojo
 * Si la logitud de la busqueda es igual a 0 entonces mostramos corazon gris
 * Al hacer click en "me gusta" pedimos ip
 * comparamos ip =>verifyIP()
 *Si cumple la condicion entonces registramos el "megusta" =>like()
 */
</script>

<style>
.megusta {
  width: 70px;
  height: 70px;
  position: relative;
}

.corazon {
  position: absolute;
  top: 10%;
  bottom: 10%;
  left: 10%;
  right: 10%;
  display: grid;
  place-items: center;
}

.contador {
  color: rgb(255, 255, 255);
  background-color: rgba(79, 91, 252, 0.6);
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  z-index: 1;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration-color: #fff;
}
</style>