<template>
  <button
    class="bg-red-500 text-white flex justify-items-end"
    @click="cerrarSesion()"
  >
    Salir
  </button>
</template>

<script>
import { auth } from "./aq1sw2";
import Swal from "sweetalert2";
export default {
  name: "CerrarSesion",
  components: {
    auth,
  },
  methods: {
    cerrarSesion() {
      auth
        .signOut()
        .then(() => {
          Swal.fire({
            title: "Bye! Bye!",
            text: "Vuelve pronto",
            icon: "success",
            confirmButtonText: "Salir",
          });
          this.$router.push("/admin");
        })
        .catch((error) => {
          Swal.fire({
            title: "Algo esta mal",
            text: error.message,
            icon: "warning",
            confirmButtonText: "Salir",
          });
        });
    }, //cerrar ultimo metodos
  },
};
</script>

