<template>
  <swiper
    class="w-full md:w-2/3 py-3"
    :pagination="{
      type: 'progressbar',
    }"
    :effect="'cube'"
    :navigation="true"
    :scrollbar="{
      hide: true,
    }"
    :loop="true"
    :slidesPerView="1"
    :grabCursor="true"
    :cubeEffect="{
      shadow: true,
      slideShadows: true,
      shadowOffset: 20,
      shadowScale: 0.8,
    }"
  >
    <swiper-slide v-for="img in images" :key="img.index">
      <img :src="img.url" :alt="img.title" class="md:h-2/3" />
      <!-- <img src="https://picsum.photos/500" alt="img.title" /> -->
    </swiper-slide>
  </swiper>
</template>
<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/swiper.scss";
import "swiper/components/pagination/pagination.min.css";
import "swiper/components/navigation/navigation.min.css";
import "swiper/components/scrollbar/scrollbar.min.css";
import "swiper/components/effect-cube/effect-cube.min.css";
import SwiperCore, {
  EffectCube,
  Scrollbar,
  Navigation,
  Pagination,
} from "swiper/core";
import { db } from "./aq1sw2";
SwiperCore.use([EffectCube, Scrollbar, Navigation, Pagination]);

const imagesURL = db.collection("imagesURL");
export default {
  name: "SliderBody",
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      images: [],
    };
  },
  mounted() {
    this.mostrarList();
  },
  methods: {
    async mostrarList() {
      await imagesURL
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            if (
              doc.data().categoria === "Slider" ||
              doc.data().categoria === "slider"
            ) {
              this.images.push({
                name: doc.data().name,
                url: doc.data().url,
                categoria: doc.data().categoria,
              });
              // this.$mount();
            }
          });
        })
        .catch((error) => console.log(error));
    }, //mostrarList
  },
};
</script>
