<template>
  <div
    class="mx-auto grid bio grid-cols-1 xl:px-8 xl:py-3 xl:gap-x-8 py-0"
    id="Bio"
  >
    <div
      class="
        relative
        z-10
        col-start-1
        row-start-1
        px-4
        pt-40
        xl:pt-0
        pb-3
        xl:pb-0
        bg-gradient-to-t
        from-black
        xl:bg-none
      "
    >
      <h2
        class="
          text-xl
          font-semibold
          text-white
          sm:text-2xl
          sm:leading-7
          xl:text-black
          md:text-3xl
        "
      >
        Sobre mi
      </h2>
      <div class="text-base font-normal mx-2 order-1 text-justify">
        <p class="text-white xl:text-black">
          Soy msico violinista con mas de 20 aos de experiencia, resido en
          <button
            href="#bio"
            class="text-yellow-400 font-semibold rounded-lg"
            @click="ventana('PuntaCana')"
            @mousemove="state = false"
          >
            Punta Cana
          </button>
          por tanto es el rea donde hago vida musical. Como violinista hago
          msica de cualquier genero, aunque en la actualidad mi repertorio se
          basa mayormente en el genero de la msica moderna, con ese concepto he
          sido invitado para interpretar el violin numerosas bodas y cocktailes
          y hoteles de la regin.
        </p>
      </div>
    </div>
    <div
      class="
        col-start-1
        row-start-1
        flex
        xl:col-start-2
        xl:row-span-3
        xl:hidden
      "
    >
      <div class="w-full grid grid-cols-3 grid-rows-2 gap-2 relative">
        <div class="col-span-3 row-span-2 md:col-span-2">
          <!-- https://firebasestorage.googleapis.com/v0/b/omarsuniagaweb.appspot.com/o/Slider%2FPortada%20Disco.png?alt=media&token=70139862-6450-48bf-93f8-6ea97288cb80 -->
          <img
            class="absolute inset-0 w-full h-full object-cover bg-gray-100"
            :src="cabeceraBio"
            alt=""
          />
        </div>
      </div>
    </div>
  </div>
  <div class="grid grid-cols-1 md:grid-cols-2 relative">
    <div
      class="text-base m-5 md:mx-2 pt-4 font-normal py-5 order-3 text-justify"
    >
      <p class="text-black text-sm font-medium">Estudios y Preparacin</p>
      <p>
        Empec mis estudios musicales en la
        <a
          href="#bio"
          class="text-yellow-400 font-semibold text-justify"
          @click="ventana('OSIJNE')"
        >
          Orquesta Sinfnica Infantil del Estado Nueva Esparta
        </a>
        -
        <a
          href="#bio"
          class="text-yellow-400 font-semibold"
          @click="ventana('Venezuela')"
        >
          Venezuela</a
        >, haciendo msica clsica y estudiando la interpretacin acadmica del
        instrumento.
      </p>
      <p>
        Aos despus y en paralelo a la Orquesta, cre un ensamble de msica
        Venezolana, llamada
        <a
          href="#bio"
          class="text-yellow-400 font-semibold"
          @click="ventana('Bitacora')"
        >
          Bitcora</a
        >. Donde aprend entre muchas cosas el arte de la improvisacin, del
        acompaamiento, de mezclar y adaptar generos musicales, de arreglar,
        componer y
        <a
          href="#bio"
          class="text-yellow-400 font-medium rounded-lg"
          @click="ventana('ProduccionMusical')"
        >
          producir msica
        </a>
        abajo un ejemplo de una de mis composiciones
      </p>
      <!-- <Reproductor /> -->
      <p>
        Adems de violinista, soy graduado en Informtica donde aun me mantengo
        en el rea
        <a
          href="#bio"
          class="text-yellow-400 font-medium rounded-lg"
          @click="ventana('FrontEnd')"
        >
          Front-end Development
        </a>
         programacin web y
        <a
          href="#bio"
          class="text-yellow-400 font-medium rounded-lg"
          @click="ventana('MCEAV')"
        >
          Mster en Comunicacin y Educacin Audiovisual
        </a>
        con mas de 10 aos de experiencia docente.
      </p>
    </div>
    <div class="order-2 object-cover">
      <Slider />
    </div>
    <TransitionRoot appear :show="isOpen" as="template">
      <Dialog as="div" @close="closeModal">
        <div class="fixed inset-0 z-10 overflow-y-auto">
          <div class="min-h-screen px-4 text-center">
            <TransitionChild
              as="template"
              enter="duration-300 ease-out"
              enter-from="opacity-0"
              enter-to="opacity-100"
              leave="duration-200 ease-in"
              leave-from="opacity-100"
              leave-to="opacity-0"
            >
              <DialogOverlay class="fixed inset-0" />
            </TransitionChild>

            <span class="inline-block h-screen align-middle" aria-hidden="true">
              &#8203;
            </span>

            <TransitionChild
              as="template"
              enter="duration-300 ease-out"
              enter-from="opacity-0 scale-95"
              enter-to="opacity-100 scale-100"
              leave="duration-200 ease-in"
              leave-from="opacity-100 scale-100"
              leave-to="opacity-0 scale-95"
            >
              <div
                class="
                  inline-block
                  w-full
                  max-w-md
                  p-6
                  my-8
                  overflow-hidden
                  text-left
                  align-middle
                  transition-all
                  transform
                  bg-white
                  shadow-xl
                  rounded-2xl
                "
              >
                <DialogTitle
                  as="h3"
                  class="text-lg font-medium leading-7 text-gray-900 mt-3"
                >
                  {{ title }}
                </DialogTitle>
                <div class="grid grid-cols-2">
                  <img
                    class="
                      md:w-full
                      md:h-full
                      w-11/12
                      mx-auto
                      rounded-md
                      col-span-2
                      md:col-span-1
                    "
                    :src="img"
                    :alt="title"
                  />
                  <p
                    class="
                      text-sm text-gray-500
                      mx-3
                      overflow-auto
                      h-32
                      col-span-2
                      md:col-span-1
                      flex
                      text-justify
                      mt-3
                    "
                  >
                    {{ description }}
                  </p>
                </div>

                <div class="mt-4">
                  <button
                    type="button"
                    class="
                      inline-flex
                      justify-center
                      px-4
                      py-2
                      text-sm
                      font-medium
                      text-blue-900
                      bg-blue-100
                      border border-transparent
                      rounded-md
                      hover:bg-blue-200
                      focus:outline-none
                      focus-visible:ring-2
                      focus-visible:ring-offset-2
                      focus-visible:ring-blue-500
                    "
                    @click="closeModal"
                  >
                    Cerrar
                  </button>
                </div>
              </div>
            </TransitionChild>
          </div>
        </div>
      </Dialog>
    </TransitionRoot>
  </div>
  <br />
</template>

<script>
import {
  TransitionRoot,
  TransitionChild,
  Dialog,
  DialogOverlay,
  DialogTitle,
} from "@headlessui/vue";
import { db } from "./aq1sw2";
import Slider from "@/components/Slider.vue";
import Reproductor from "@/components/Reproductor.vue";
export default {
  name: "Bio",
  components: {
    Slider,
    Reproductor,
    TransitionRoot,
    TransitionChild,
    Dialog,
    DialogOverlay,
    DialogTitle,
  },
  data() {
    return {
      title: "",
      description: "",
      img: "",
      isOpen: false,
      cabeceraBio: this.cabecera(),
    };
  },
  methods: {
    ventana(text) {
      const biblioteca = db.collection("biblioteca").doc(text);
      biblioteca
        .get()
        .then((doc) => {
          if (doc.exists) {
            this.pintarVentana(text, doc.data().text, doc.data().image);
          } else {
            console.log("No such document!");
          }
        })
        .catch((error) => {
          console.log("Error getting document:", error);
        });
    },
    closeModal() {
      this.isOpen = false;
    },

    pintarVentana(text, description, img) {
      this.isOpen = true;
      this.title = text;
      this.description = description;
      this.img = img;
    },
    async cabecera() {
      db.collection("imagesURL")
        .where("categoria", "==", "cabeceraBio")
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            return (this.cabeceraBio = doc.data().url);
          });
        });
    },
  },
};
</script>
<style>
.bio {
  background-color: #fbfcfc;
}
</style>