<template>
  <form class="p-4 flex justify-around" v-on:submit.prevent="upload()">
    <input
      type="file"
      id="file"
      ref="myFiles"
      class="custom-file-input shadow-xl bg-gray-100"
      @change="previewFiles"
      multiple
    />
    <input
      type="text"
      placeholder="Categoria"
      v-model="categoria"
      class="bg-gray-100 shadow-xl"
    />
    <button
      type="submit"
      class="bg-red-300 hover:bg-pink-600 shadow-md rounded-md px-1"
    >
      Upload
    </button>
  </form>
  <div class="w-full h-36 mx-auto grid grid-cols-3 grid-rows-2 gap-4">
    <ul v-for="item in date" :key="item.url" class="flex justify-around">
      <li v-if="item.url" class="bg-green-400 hover:bg-green-700">
        <p>{{ item.name }}</p>
        <p>{{ item.categoria }}</p>
        <img
          class="shadow-xl"
          :src="item.url"
          id="myimg"
          :alt="item.name"
          width="150"
          height="150"
        />
      </li>
    </ul>
  </div>
</template>

<script>
import { db, storage } from "./aq1sw2";
import { tmpdir } from "os";
import { join, dirname, basename, extname } from "path";

const imagesURL = db.collection("imagesURL");
export default {
  name: "uploadPhoto",
  // components: {fs, tmpdir, dirname},
  data() {
    return {
      files: [],
      categoria: "",
      date: [],
    };
  },
  watch: {
    date: function (value) {
      console.log(value);
    },
  },
  mounted() {
    this.mostrarList();
  },
  methods: {
    previewFiles() {
      this.files = this.$refs.myFiles.files[0];
    },
    // async procesarImg() {
    //   const filePath = this.files.name;
    //   const contentType = this.files.type;
    //   if (filePath && contentType) {
    //     // Exit if this is triggered on a file that is not an image.
    //     if (!contentType.startsWith("image/")) {
    //       console.log("This is not an image.");
    //       return true;
    //     }

    //     // Get the file name.
    //     const extendName = extname(filePath);
    //     const fileName = basename(filePath, extendName);
    //     const fileFullName = `${fileName}${extendName}`;
    //     if (fileName.includes("_thumb_")) {
    //       console.log("Already a Thumbnail.");
    //       return true;
    //     }
    //     const sharp = require("sharp");
    //     console.log(sharp);
    //     // const sizes = [480, 640, 1200];
    //     const helperImg = (filePath, fileName, size = 320) => {
    //       return sharp(filePath)
    //         .resize(size)
    //         .toFile(fileName + ".jpg", (err, info) => {
    //           console.log(err);
    //         });
    //     };
    //     console.log(helperImg);
    //   } else {
    //     console.log("Incomplete Data");
    //     return false;
    //   }
    // },
    upload() {
      if (!this.files) {
        console.log("error");
      } else {
        const ubicacionTitulo = storage.ref(
          "/" + this.categoria + "/" + this.files.name
        );
        const almacenando = ubicacionTitulo.put(this.files);

        almacenando
          .then((snapshot) => console.log("Archivo Guardardo"))
          .then((snapshot) => {
            ubicacionTitulo.getDownloadURL().then((url) => {
              imagesURL.add({
                id: Date.now(),
                url: url,
                name: this.files.name,
                categoria: this.categoria,
              });
            });
          })
          .catch((error) => console.log(error));
        this.mostrarList();
      } //else
    }, //upload
    async mostrarList() {
      try {
        await imagesURL.doc().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.date.push({
              id: doc.data().id,
              name: doc.data().name,
              url: doc.data().url,
              categoria: doc.data().categoria,
            });
          });
        });
      } catch (error) {
        return error;
      }
    }, //mostrarList
  }, //method
}; //export default
</script>

